import React from "react"
import resolveConfig from "tailwindcss/resolveConfig"

import tailwindConfig from "../../tailwind.config.js"
import WaveContainer from "../components/common/WaveContainer"
import Button from "./common/Button"

import { IconArrowShort, IconLogo, IconPlay } from "../components/common/icons"
import aboutImage from "../images/about-image.png"
import videoImage from "../images/video-image.png"

const twConfig = resolveConfig(tailwindConfig)
const lgScreen = parseInt(twConfig.theme.screens.lg, 10)

const Box = ({ className, children, ...props }) => (
  <div
    className={`flex-1 flex-grow pt-36 pb-16 px-14 rounded-xl ${
      className || ""
    }`}
    {...props}
  >
    {children}
  </div>
)

const list = [
  "We combine quality image capture with a minimalistic production, capturing the most authentic moments.",
  "Together, we are intensely curious and on a search towards unique designs, concepts and materials.",
  "We are intrinsically drawn to beauty and always on the lookout for original ideas.",
]

export default () => {
  const [windowWidth, setWindowWidth] = React.useState(0)
  React.useEffect(() => {
    function updateWidth() {
      setWindowWidth(window.innerWidth)
    }
    window.addEventListener("resize", updateWidth)
    updateWidth()
    return () => window.removeEventListener("resize", updateWidth)
  }, [])

  return (
    <section>
      <WaveContainer
        startColor="n-200"
        endColor="n-800"
        className="relative z-10"
      >
        <div className="container py-28 text-3.5xl">
          <div className="md:px-14 font-light">
            <div className="relative left-px text-5xl mb-6">
              <IconLogo className="relative left-px" />
            </div>
            Epic Elite is a one-stop shop for all things{" "}
            <br className="hidden lg:inline" />
            YouTube.{" "}
            <span className="text-n-400">
              We offer personalized and dedicated services to each of our
              clients.
            </span>
          </div>
        </div>

        {windowWidth >= lgScreen && <WaveContainer.Wave adjust={50} />}

        <div className="container flex flex-col lg:flex-row lg:space-x-6 space-y-20 lg:space-y-0">
          <Box className="bg-n-900 text-n-200">
            <h2 className="text-2xl font-medium mb-5">
              Epic Elite only serves a very limited and exclusive clientele.
            </h2>
            <p className="text-xl font-light">
              If you feel you meet the criteria, kindly apply for an account and
              one of our representatives will get in touch.
            </p>
            <Button type="secondary" className="mt-11 lg:mt-14">
              Apply for Account
              <IconArrowShort className="ml-3 relative bottom-px text-90" />
            </Button>
          </Box>
          <Box className="bg-primary">
            <p className="text-2xl">
              Our holistic approach towards managing your catalog online not
              only results in generating revenue but also boosting your numbers
              across all social media platforms.
            </p>
            <Button className="mt-11 lg:mt-14">
              Sign In
              <IconArrowShort className="ml-3 relative bottom-px text-90" />
            </Button>
          </Box>
        </div>

        {windowWidth < lgScreen && <WaveContainer.Wave adjust={-150} />}

        <div className="bg-about-image bg-about-center-bottom lg:bg-about-center bg-no-repeat pb-600 lg:pb-0">
          <div className="container py-40">
            <p className="text-3.5xl text-white lg:w-1/2 font-medium">
              A <span className="text-primary">holistic approach </span>
              towards managing your online catalog.
            </p>
          </div>
        </div>

        <div className="container flex flex-row justify-between flex-wrap mt-6 pb-10 sm:pb-0">
          {list.map((item, index) => (
            <p className="text-white text-xl font-light block sm:w-240 mb-16 sm:mb-24 pr-4">
              <span className="font-mono text-primary text-sm font-normal">
                {"0" + (index + 1)}{" "}
              </span>
              {item}
            </p>
          ))}
        </div>
      </WaveContainer>

      <WaveContainer startColor="n-800" endColor="n-200">
        <div className="container">
          <div className="rounded-xl overflow-hidden relative">
            <img src={videoImage} alt="Video" className="h-full object-cover" />
            <div className="absolute top-0 right-0 bottom-0 left-0 bg-gradient-to-t from-black to-transparent w-full h-full flex items-end">
              <div className="w-full py-4 px-6 text-n-200 flex flex-row items-center justify-between">
                <div className="flex flex-row items-center">
                  <button className="focus:outline-none absolute lg:static top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 lg:translate-x-0 lg:translate-y-0">
                    <IconPlay className="text-4xl" />
                  </button>
                  <p className="text-lg font-light lg:ml-4">
                    See how we help musicians, labels and channels.
                  </p>
                </div>
                <span className="font-mono text-sm hidden lg:inline">00:17 / 01:24</span>
              </div>
            </div>
          </div>
        </div>
        <WaveContainer.Wave adjust={-200} />
      </WaveContainer>
    </section>
  )
}
